@import "./page-base.css";

.training .card {
    max-width: 520px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    box-sizing: border-box;
}

.training .card select,
.training .card input,
.training .card button {
    width: 100%;
    box-sizing: border-box;
}

.training .card input[type="file"] {
    margin-top: 0.25rem;
    padding: 0.4rem;
    background: rgba(14,14,14,0.8);
    border-radius: 10px;
}
