.codefix-page {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.codefix-section {
  background: #080b11;
  border: 1px solid #1f2937;
  border-radius: 16px;
  padding: 1.2rem;
}

.codefix-section.controls {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.controls-row {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.control-group {
  flex: 1;
  min-width: 180px;
  display: flex;
  flex-direction: column;
}

.upload-btn {
  background: linear-gradient(135deg, #0096ff, #00d4ff);
  border: none;
  color: #011627;
  border-radius: 10px;
  padding: 0.65rem 1.2rem;
  font-weight: 600;
  cursor: pointer;
  width: fit-content;
}

.file-meta {
  font-size: 0.9rem;
  color: #cbd5f5;
}

.model-label {
  font-size: 0.9rem;
  margin-bottom: 0.3rem;
  color: #cbd5f5;
}

.model-select {
  background: #0b1220;
  border: 1px solid #263449;
  border-radius: 8px;
  color: #e3f2ff;
  padding: 0.45rem 0.65rem;
  width: 100%;
}

.run-btn {
  margin-top: 0.75rem;
  background: linear-gradient(135deg, #24c6dc, #514a9d);
  border: none;
  border-radius: 10px;
  color: #f7fbff;
  padding: 0.6rem 1.2rem;
  font-weight: 600;
  cursor: pointer;
  width: fit-content;
}

.run-btn[disabled] {
  opacity: 0.6;
  cursor: not-allowed;
}

.status {
  font-size: 0.9rem;
  color: #9ac0ff;
}

.status.error {
  color: #ff7b7b;
}

.summary-box {
  background: rgba(0, 150, 255, 0.07);
  border: 1px solid rgba(0, 150, 255, 0.25);
  border-radius: 12px;
  padding: 0.9rem;
}

.summary-box h3 {
  margin-top: 0;
  margin-bottom: 0.4rem;
}

.codefix-actions {
  display: flex;
  gap: 0.75rem;
}

.codefix-actions button {
  flex: 1;
  border-radius: 10px;
  border: 1px solid #1f2937;
  background: #111827;
  color: #d4ecff;
  padding: 0.6rem;
  cursor: pointer;
}

.code-viewer .code-view__header {
  padding: 0.9rem 1rem;
  border-bottom: 1px solid #1f2937;
  font-weight: 500;
  color: #9fb5d4;
}

.code-viewer .code-view__body {
  flex: 1;
  overflow: auto;
  padding: 0.5rem 0.75rem;
  background: #050607;
}

.code-view pre {
  margin: 0 !important;
  box-shadow: none !important;
  background: transparent !important;
}

.code-view__body pre[class*="language-"],
.code-view__body pre[class*="language-"] > code,
.code-view__body code[class*="language-"] {
  background: transparent !important;
  box-shadow: none !important;
}

.code-view__body .token-line {
  display: block;
  background: transparent !important;
}

@media (max-width: 768px) {
  .controls-row {
    flex-direction: column;
  }
}
