@import "./page-base.css";

.sql .grid {
    margin-top: 1rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 1.25rem;
}

.sql .grid > div {
    background: rgba(12, 12, 12, 0.85);
    border: 1px solid #1f1f1f;
    border-radius: 18px;
    padding: 1rem 1.2rem;
    box-shadow: 0 12px 30px rgba(0,0,0,0.35);
}

.sql textarea { 
    height: 240px; 
    font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace; 
    width: 100%;
    box-sizing: border-box;
}

.sql .pairs-wrapper {
    max-height: 320px;
    overflow: auto;
    border: 1px solid #222;
    border-radius: 12px;
}

.sql table { width:100%; border-collapse: collapse; table-layout: fixed; }
.sql th, .sql td { 
    padding: .5rem .6rem; 
    border-bottom: 1px solid #222;
    vertical-align: top; 
}
.sql .pairs pre { margin: 0; white-space: pre-wrap; word-break: break-word; }
.sql .actions {
    display: flex;
    gap: 0.75rem;
    margin-bottom: 0.6rem;
    flex-wrap: wrap;
}
.sql .actions button {
    flex: 1;
    min-width: 140px;
}

.sql .trainer-card {
    margin-top: 1.5rem;
    background: rgba(12, 12, 12, 0.85);
    border: 1px solid #1f1f1f;
    border-radius: 18px;
    padding: 1.2rem;
    max-width: 520px;
    box-shadow: 0 12px 30px rgba(0,0,0,0.35);
}

.sql .trainer-card .note {
    margin-top: 0.6rem;
    color: #9fb5d4;
    font-size: 0.9rem;
}
.sql .metrics {
    display: flex;
    gap: 1rem;
    margin-bottom: 0.6rem;
    font-size: 0.9rem;
    color: #9fb5d4;
    flex-wrap: wrap;
}
.sql .metric-card {
    background: rgba(17, 24, 39, 0.55);
    border: 1px solid rgba(79, 209, 255, 0.25);
    border-radius: 12px;
    padding: 0.65rem 1rem;
    flex: 1;
    min-width: 150px;
    box-sizing: border-box;
}

@media (max-width: 700px) {
    .sql .grid {
        grid-template-columns: minmax(0, 1fr);
    }
}
