@import "./page-base.css";

.analysis .card {
    max-width: 520px;
    width: 100%;
    display: flex;
    gap: 0.75rem;
    flex-wrap: wrap;
    align-items: center;
}

.analysis .card input[type="file"] {
    flex: 1;
    min-width: 220px;
    padding: 0.45rem;
    border-radius: 12px;
    background: rgba(14,14,14,0.85);
    border: 1px solid #232323;
    color: #d8e6ff;
}

.analysis .card button {
    flex-shrink: 0;
}
